<template>
  <div id="list">
  	<ul>
  		<li v-for="item in hrs" :class="{ active: currentSessionId?item.username === currentSessionId.username:false }"
			v-on:click="changeCurrentSessionId(item)"><!--   :class="[item.id === currentSessionId ? 'active':'']" -->
  			<img class="avatar" :src="item.userface" :alt="item.name">
  			<p class="name">{{item.name}}</p>
  		</li>
  	</ul>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'list',
  data () {
    return {

    }
  },
  computed: mapState([
  'hrs',
  'currentSessionId'
	]),
  methods:{
  	changeCurrentSessionId:function (currentSessionId) {
  		this.$store.commit('changeCurrentSessionId',currentSessionId)
  	}
  }
}
</script>

