<template>
    <div>
        <el-container>
            <el-header class="homeHeader">
                <div class="title">
                    微人事
                </div>
                <div>
                    <el-button @click="goChat" icon="el-icon-bell" style="margin-right: 10px;color: #000;" size="mini"></el-button>
                    <el-dropdown class="userInfo" @command="commandHandler">
                    <span class="el-dropdown-link">
                    用户：{{user.name}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userinfo">个人中心</el-dropdown-item>
                            <el-dropdown-item command="setting">设置</el-dropdown-item>
                            <el-dropdown-item command="logout" divided>注销登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>

            </el-header>
            <el-container>
                <el-aside width="200px">
                        <el-menu router>
                            <el-submenu :index="index+''" v-for="(item,index) in routes" v-if="!item.hidden"
                            :key="index">
                                <template slot="title">
                                    <i class="el-icon-location"></i>
                                    <span>{{item.name}}</span>
                                </template>
                                    <el-menu-item :index="child.path" v-for="(child,indexj) in item.children"
                                    :key="indexj">{{child.name}}</el-menu-item>
                            </el-submenu>
                        </el-menu>

                </el-aside>
                <el-main>
                    <el-breadcrumb separator-class="el-icon-arrow-right" >
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item v-if="this.$router.currentRoute.path!='/home'">{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <router-view class="routeViewCss"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "home",
        data(){
            return{
                user:JSON.parse(window.sessionStorage.getItem("user"))
            }
        },
        computed:{
          routes(){
              return this.$store.state.routes
          }
        },
        methods:{
            goChat(){
                this.$router.push("/chat")
            },
            /*menuClick(index){
                //console.log(index)
                this.$router.push(index)
            },*/
            commandHandler(cmd){
                if(cmd=='logout'){
                    this.$confirm('此操作将注销登录, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest("/logout")
                        window.sessionStorage.removeItem("user")
                        this.$store.commit("initRoutes",[])
                        this.$router.replace("/")
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }
            }
        }
    }
</script>

<style>
    .routeViewCss{
        margin-top: 20px;
    }
.homeHeader{
    background-color: #5bb6ff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:0px 15px;
    box-sizing:border-box;
}
    .homeHeader .title{
        font-size: 30px;
        font-family: 华文行楷;
        color: #fffdd1;
    }
    .homeHeader .userInfo{
        cursor: pointer;
    }
</style>